import mainRoutes from '../mainLink.js'

function getMetaTitle(name) {
  const r = mainRoutes.find(route => route.name === name)
  return r?.meta?.title || { kz: '', ru: '' }
}

function Route(name) {
  return {
    path: `/menu-${name.toLowerCase()}`,
    name: `Menu${name}`,
    component: () => import(`@/views/pagesLink/food/menu/${name}.vue`),
    meta: {
      title: getMetaTitle('Menu')
    }
  }
}

export default [
  Route('15-19.09.2025'),
  Route('02-05.09.2025'),
  Route('08-12.09.2025')
]