import mainRoutes from '../mainLink.js'

function getMetaTitle(name) {
  const r = mainRoutes.find(route => route.name === name)
  return r?.meta?.title || { kz: '', ru: '' }
}

function Route(name) {
  return {
    path: `/${name.toLowerCase()}`,
    name: `${name}`,
    component: () => import(`@/views/pagesLink/food/food/${name}.vue`),
    meta: {
      title: getMetaTitle('Food')
    }
  }
}

export default [
  Route('daily-menu'),
  Route('healthy-eating')
]